<template>
  <q-page class="bg-grey-2 fullscreen auth">
    <q-toolbar class="text-seconday">
      <q-btn flat round dense icon="mdi-arrow-left" to="/magazine" />
      <q-toolbar-title> Leitor de QrCode </q-toolbar-title>
    </q-toolbar>
    <q-card
      class="absolute-center"
      flat
      :style="$q.screen.name == 'xs' ? 'min-width: 352px' : 'min-width: 480px'"
    >
      <q-card-section class="q-pa-none">
        <qrcode-drop-zone @decode="onDecode" @init="logErrors">
          <qrcode-stream @decode="onDecode" @init="onInit" />
        </qrcode-drop-zone>
        <qrcode-capture v-if="noStreamApiSupport" @decode="onDecode" />
      </q-card-section>
    </q-card>
  </q-page>
</template>

<script>
import { QrcodeStream, QrcodeDropZone, QrcodeCapture } from "vue-qrcode-reader";
export default {
  components: { QrcodeStream, QrcodeDropZone, QrcodeCapture },
  data() {
    return {
      result: "",
      noStreamApiSupport: false,
      error: "",
    };
  },
  watch: {
    result() {
      this.$emit("searchQrcode", this.result);
    },
  },
  methods: {
    onDecode(result) {
      this.result = result;
    },

    logErrors(promise) {
      promise.catch(console.error);
    },

    async onInit(promise) {
      try {
        await promise;
      } catch (error) {
        if (error.name === "NotAllowedError") {
          this.error = "ERROR: you need to grant camera access permisson";
        } else if (error.name === "NotFoundError") {
          this.error = "ERROR: no camera on this device";
        } else if (error.name === "NotSupportedError") {
          this.error = "ERROR: secure context required (HTTPS, localhost)";
        } else if (error.name === "NotReadableError") {
          this.error = "ERROR: is the camera already in use?";
        } else if (error.name === "OverconstrainedError") {
          this.error = "ERROR: installed cameras are not suitable";
        } else if (error.name === "StreamApiNotSupportedError") {
          this.noStreamApiSupport = true;
          this.error = "ERROR: Stream API is not supported in this browser";
        }
      }
    },
  },
};
</script>

